import React, { useEffect, useRef } from "react";
import "./App.css"; // Import styles
import gsap from "gsap";
import Lenis from "@studio-freight/lenis";

// Ensure to import necessary modules from webgi
import {
  ViewerApp,
  AssetManagerPlugin,
  SSRPlugin,
  GBufferPlugin,
  ProgressivePlugin,
  TonemapPlugin,
  SSAOPlugin,
  GroundPlugin,
  FrameFadePlugin,
  DiamondPlugin,
  BloomPlugin,
  TemporalAAPlugin,
  RandomizedDirectionalLightPlugin,
  Color,
} from "webgi";

const App = () => {
  const canvasRef = useRef(null);

  useEffect(() => {
    const lenis = new Lenis({
      duration: 2.2,
      easing: (t) => Math.min(1, 1.001 - Math.pow(2, -5 * t)),
      direction: "vertical",
      gestureDirection: "vertical",
      mouseMultiplier: 1,
    });

    function raf(time) {
      lenis.raf(time);
      requestAnimationFrame(raf);
    }

    requestAnimationFrame(raf);

    const diamondsObjectNames = [
      "diamonds",
      "diamonds001",
      "diamonds002",
      "diamonds003",
      "diamonds004",
      "diamonds005",
    ];

    const setupViewer = async () => {
      const canvas = canvasRef.current;
      const viewer = new ViewerApp({
        canvas,
        useGBufferDepth: true,
        isAntialiased: false,
      });

      const manager = await viewer.addPlugin(AssetManagerPlugin);
      await viewer.addPlugin(GBufferPlugin);
      await viewer.addPlugin(new ProgressivePlugin(32));
      await viewer.addPlugin(new TonemapPlugin(true, false));
      await viewer.addPlugin(SSRPlugin);
      await viewer.addPlugin(SSAOPlugin);
      await viewer.addPlugin(FrameFadePlugin);
      await viewer.addPlugin(GroundPlugin);
      await viewer.addPlugin(BloomPlugin);
      await viewer.addPlugin(TemporalAAPlugin);
      await viewer.addPlugin(DiamondPlugin);
      await viewer.addPlugin(RandomizedDirectionalLightPlugin, false);
      viewer.setBackground(new Color("#EEB7B5").convertSRGBToLinear());

      // Load model and other WebGi logic here...
      await manager.addFromPath("./assets/ring_webgi.glb");
    };

    setupViewer();

    // Cleanup on unmount
    return () => {
      lenis.destroy();
    };
  }, []);

  const downloadImage = () => {
    const canvas = canvasRef.current;
    if (canvas) {
      const link = document.createElement("a");
      link.download = "webgi-rendered-image.png";
      link.href = canvas.toDataURL("image/png");
      link.click();
    }
  };

  return (
    <div>
      <div className="loader">
        <svg
          width="48"
          height="48"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <mask
            id="a"
            style={{ maskType: "alpha" }}
            maskUnits="userSpaceOnUse"
            x="9"
            y="3"
            width="32"
            height="42"
          >
            <path
              d="M25 44a15 15 0 1 0 0-30 15 15 0 0 0 0 30Z"
              stroke="#fff"
              strokeWidth="2"
              strokeLinecap="round"
              strokeLinejoin="round"
            />
            <path
              d="m18 8 3-4h8l3 4-7 6-7-6Z"
              fill="#555"
              stroke="#fff"
              strokeLinecap="round"
              strokeLinejoin="round"
            />
          </mask>
          <g mask="url(#a)">
            <path d="M0 0h48v48H0V0Z" fill="#52322B" />
          </g>
        </svg>
        <p>Loading... Please wait</p>
        <div className="progress"></div>
      </div>

      <section className="header">
        <div className="header--container">
          <h1 className="header--brand">
            DESIGN
            <span>
              <a
                href="https://www.neotix.com.br/lab"
                target="_blank"
                rel="noreferrer"
              >
                {" "}
                BY MEGIFT
              </a>
            </span>
          </h1>
          <ul className="header--menu">
            <li className="know--more">
              <a
                href="https://www.neotix.com.br/lab"
                target="_blank"
                rel="noreferrer"
              >
                Know more
              </a>
            </li>
            <li className="music--control">
              {/* Music Control Icon */}
              <svg
                width="24"
                height="24"
                viewBox="0 0 24 24"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M11 5L6 9H2V15H6L11 19V5Z"
                  stroke="#52322B"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
                <path
                  d="M19.07 4.92999C20.9447 6.80527 21.9979 9.34835 21.9979 12C21.9979 14.6516 20.9447 17.1947 19.07 19.07M15.54 8.45999C16.4774 9.39763 17.004 10.6692 17.004 11.995C17.004 13.3208 16.4774 14.5924 15.54 15.53"
                  stroke="#52322B"
                  strokeLinecap="round"
                  strokeLinejoin="round"
                />
              </svg>
            </li>
          </ul>
        </div>
      </section>

      <div className="side-bar">
        <nav className="navigation">
          <ul>
            <li className="unique"></li>
            <li className="forever"></li>
            <li className="emotions"></li>
          </ul>
        </nav>
      </div>

      <section className="section cam-view-1">
        <div className="hero--container">
          <div className="hero--content">
            <h1>
              Unique
              <br />
              Jewelry
            </h1>
            <div className="hero--text">
              <p>
                Customize your unique ring with graphic angles and pure lines
                that combine to create the pure beauty of the collection.
              </p>
              <button className="button button-scroll">Start now</button>
            </div>
          </div>
        </div>
      </section>

      <div className="exit--container">
        <button onClick={downloadImage} className="button--secondary">
          Download Image
        </button>
      </div>

      <div id="webgi-canvas-container">
        <canvas ref={canvasRef} id="webgi-canvas"></canvas>
      </div>
    </div>
  );
};

export default App;
